<template>
    <div 
    :style="{ backgroundImage: 'url(' + background + ')'}"
    class="container-fluid pt-4">

        <b-container>
            <HMenu/>
            <div id='headerColab' class="center text-white pt-3 pb-4 pl-4 ">
                <p class="stylingH1 pb-3"> Buzón</p>
            </div>
            <b-row>                
                <b-col class="bg-gris py-4 px-5" style="margin-bottom: 5em;">              
                    <div class="row pager">
                        <div v-for="(question, index) in quiz.questions" :key="question.id">
                            <div v-if="index === questionIndex">
                                <p>0{{index + 1}} / 05</p>
                            </div>
                        </div>                        
                    </div>
                    <p v-if="errors" class="alert alert-danger mb-0">
                            {{ error_msg }}
                    </p>
                    <div class="row question">
                        <div v-for="(question, index) in quiz.questions" :key="question.id">
                            <div v-if="index === questionIndex">
                            
                                <h4 class="my-5 px-3">{{ question.text }}</h4>                                 
                                <div v-for="answer in question.answers" :key="answer.id" class="selectionButtons">
                                    <template v-if="answer.text === 'libre'">
                                        <b-form-textarea
                                        class="p-2 textArea"
                                        id="textarea"
                                        v-model="responses[index]"
                                        rows="3"
                                        max-rows="6"
                                        ></b-form-textarea>
                                    </template>
                                    <template v-else>  
                                        <template v-if="eleccion == false && indexSelected == ''">
                                            <label @click="SET_PICKED(true), SET_INDEX(answer.text)"
                                            :class="['form-check-label', 'formChek',{ 'blueColor' : answer.text == 'Si' , 'orangeColor' : answer.text == 'No'}]">
                                                <input type="radio" class="form-check-input"
                                                    :value="answer.value"                                                
                                                    :name="index"                                                                                                                                                 
                                                    v-model="responses[index]">
                                                <label mb-0>{{answer.text}} <img v-if="answer.text == 'Si'" :src="yesIcon" class="btn-icon ml-2"><img v-if="answer.text == 'No'" :src="noIcon" class="ml-2 btn-icon"></label>
                                            </label>
                                        </template>
                                        <template v-else>
                                            <template v-if="indexSelected == 'Si'">
                                                <label @click="SET_PICKED(true), SET_INDEX(answer.text)"
                                                :class="['form-check-label', 'formChek',{ 'orangeColor' : answer.text == 'Si' , 'whiteColor' : answer.text == 'No'}]">
                                                    <input type="radio" class="form-check-input"
                                                        :value="answer.value"                                                
                                                        :name="index"                                                                                                                                                 
                                                        v-model="responses[index]">
                                                    <label>{{answer.text}} <img v-if="answer.text == 'Si'" :src="yesIcon" class="btn-icon ml-2"><img v-if="answer.text == 'No'" :src="noIcon" class="ml-2 btn-icon"></label>
                                                </label>
                                            </template>
                                            <template v-else>
                                                 <label @click="SET_PICKED(true), SET_INDEX(answer.text)"
                                                :class="['form-check-label', 'formChek',{ 'whiteColor' : answer.text == 'Si' , 'orangeColor' : answer.text == 'No'}]">
                                                    <input type="radio" class="form-check-input"
                                                        :value="answer.value"                                                
                                                        :name="index"                                                                                                                                                 
                                                        v-model="responses[index]">
                                                    <label>{{answer.text}} <img v-if="answer.text == 'Si'" :src="yesIcon" class="btn-icon ml-2"><img v-if="answer.text == 'No'" :src="noIcon" class="ml-2 btn-icon"></label>
                                                </label>
                                            </template>
                                        </template>                 
                                    </template>  
                                </div>
                                
                                
                            </div>
                        </div>                        
                    </div>
                    <div v-if="questionIndex < (quiz.questions.length - 1)" class="mt-3">
                        <button class="btn btn-next float-right" @click="next">
                            <img :src="nextIcon" class="icon-next">
                        </button>  
                    </div>
                    <div v-else-if="questionIndex === (quiz.questions.length - 1)" class="mt-3">
                        <button
                            @click="send(responses)"
                            :class="['btn w-100', { 'saved' : sendFlag, 'btn-send' : sendFlag == false }]">
                            <img v-if="sendFlag" :src="check" class="icon mb-1 mr-1" /> {{textButton}}
                        </button>
                    </div>

                    <div v-if="questionIndex === quiz.questions.length">
                        <h3>Gracias por tus opiniones!</h3>
                        <button class="btn btn-secondary center" @click="finish()">
                            Salir
                        </button> 
                    </div>
                    
                </b-col>
                <Footer />
            </b-row>
        </b-container>   
    </div>
</template>

<script>
import img from '../assets/fondoazul.png';
import Footer from '../components/Footer';
import i_si from '../assets/icono_si.png';
import i_no from '../assets/icono_no.png';
import i_next from '../assets/flecha.png';
import {mapMutations} from 'vuex';
import HMenu from '../components/HeaderIcons';
import check from '../assets/icono_check.png';

let quiz = {
  questions: [
    {
        text: '¿Estas orgulloso de pertenecer al grupo FANOSA?',
        answers: [
            {
                text: 'Si',
                value: 1
            },
            {
                text: 'No',
                value: 0
            },
        ]
    },
    {
        text: '¿Tienes buena comunicación con tus compañeros del área?',
        answers: [
            {
                text: 'Si',
                value: 1
            },
            {
                text: 'No',
                value: 0
            },
        ]
    },
    {
        text: '¿Tu jefe te apoya e impulsa a que desempeñes tu trabajo de forma más eficiente?',
        answers: [
            {
                text: 'Si',
                value: 1
            },
            {
                text: 'No',
                value: 0
            },
        ]
    },
    {
        text: '¿Te gustan las actividades que desempeñas?',
        answers: [
            {
                text: 'Si',
                value: 1
            },
            {
                text: 'No',
                value: 0
            },
        ]
    },
    {
        text: 'Alguna sugerencia o comentario que quieras expresar (libre) ',
        answers: [
            {
                text: 'libre',
            },
        ]
    },
  ]
};

export default {
    name: 'Survey',
    components: {Footer, HMenu},
    data() {
        return {
            background: img,
            yesIcon: i_si,
            noIcon: i_no,
            nextIcon: i_next,
            quiz: quiz,
            questionIndex: 0,
            responses: [],
            error_msg: '',
            check: check,
        }
    },
    computed:{
        errors() {
            return this.$store.getters['plants/getsurveyError'];
        },
        eleccion() {
            return this.$store.getters['plants/getpickedAnswer'];
        },
        indexSelected() {
            return this.$store.getters['plants/getchoice'];
        },
        sendFlag() {
            return this.$store.getters['plants/sendFlag'];
        },
        textButton() {
            return this.$store.getters['plants/sendText'];
        },
    },
    methods: { 
        ...mapMutations('plants', ['SET_PICKED']),
        ...mapMutations('plants', ['SET_INDEX']),
        next: function() {
            if (this.responses[this.questionIndex] === undefined && this.questionIndex < 4 ) {                
                this.error_msg = 'Selecciona tu respuesta';
                this.$store.dispatch("plants/surveyError", true);
            } 
            else {    
                this.$store.dispatch("plants/surveyError", false); 
                this.$store.dispatch("plants/pickedAnswer", false);
                this.$store.dispatch("plants/setSelectedIndex", '');             
                this.questionIndex++;
            } 
        },
        send: function(responses){
            this.$store.dispatch('plants/sendSurveyInfo', {
                surveyAnswers: responses,
                uid: 'xxx' ,
                text: 'Envío exitoso',
                flag: true               
            });
            this.wait();
        },
        wait: function(){
            setTimeout(() => this.next(), 1000);
        },       
        finish: function() {
            this.questionIndex = 0;
        }
    }
    
}
</script>
