<template>
    <div class="container py-3 mx-0 px-0 bg-white sticky">

            <div class="wrapper center">
                <div class="one menu-op">
                    <router-link :to="getColabHomePath(user)" class="active">                    
                        <img :src="homeIcon" class="icon pb-1"><br>
                        Inicio
                    </router-link>
                </div>
                <div class="two menu-op">
                    <router-link :to="getBonusPath(user)">
                        <img :src="calcIcon" class="icon2 pb-1"><br>
                        Cómo calcular mi bono
                    </router-link>
                </div>
                <div class="three menu-op">
                    <router-link :to="getBuzonPath(user)">
                        <img :src="messIcon" class="icon pb-2"><br>
                        Buzón
                    </router-link>
                </div>
                <div class="four menu-op">
                    <router-link :to="getHistoryPath(user)">
                        <img :src="historyIcon" class="icon pb-1"><br>
                        Historial
                    </router-link>
                </div>
            </div>

    </div>
</template>

<script>
import i_home from '../assets/icono_home.png';
import i_calc from '../assets/icono_calculadora.png';
import i_history from '../assets/icono_historial.png';
import i_mess from '../assets/icono_buzon.png'

export default {
    name: 'Footer',
    props:{
        user : {
            required: true,
        }
    },
    data() {
        return {
            homeIcon: i_home,
            historyIcon: i_history,
            calcIcon: i_calc,
            messIcon: i_mess
        }
    },
    methods: {
        getBuzonPath: function(user){
            let normalized = this.normalize(user.city, user.depto);            
            let code = user.num;
            console.log('/'+normalized.city+'/'+normalized.depto+'/'+code+'/'+ 'buzon')
            return '/'+normalized.city+'/'+normalized.depto+'/'+code+'/'+ 'buzon';
        },
        getColabHomePath: function(user){
            let normalized = this.normalize(user.city, user.depto);            
            let code = user.num;
            console.log('/'+normalized.city+'/'+normalized.depto+'/'+code)
            return '/'+normalized.city+'/'+normalized.depto+'/'+code;
        },
        getBonusPath: function(user){    
            let normalized = this.normalize(user.city, user.depto);            
            let code = user.num;
            console.log('/'+normalized.city+'/'+normalized.depto+'/'+code+'/'+ 'calculadora')
            return '/'+normalized.city+'/'+normalized.depto+'/'+code+'/'+ 'calculadora';        
        },
        getHistoryPath: function(user){    
            let normalized = this.normalize(user.city, user.depto);            
            let code = user.num;
            console.log('/'+normalized.city+'/'+normalized.depto+'/'+code+'/'+ 'historial')
            return '/'+normalized.city+'/'+normalized.depto+'/'+code+'/'+ 'historial';        
        },
        normalize: function(cities, deptos){
            let city = cities.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s-\.]/g, "");
            let depto = deptos.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\ ]/g, "");
            return {city, depto};
        }
    },
}
</script>
